<template>
    <div class="staff">
        <el-form :inline="true" :model="staffSearch" class="staff-form">
            <div class="search-up">
                <div>
                    <el-form-item>
                        <el-input v-model="staffSearch.name" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="searchStaff"></el-button>
                        <el-button type="primary" @click="resetStaff"></el-button>
                    </el-form-item>
                </div>
                <el-form-item>
                    <el-button type="primary" @click="createStaff"></el-button>
                </el-form-item>
            </div>
            
            <div>
                <el-form-item label="">
                    <cascader-component
                        v-model="staffSearch.region_ids"
                        :cascaderName="''"
                        :setProps="setProps"
                        :requestUrl="'./admin/common/getAreaTree'"></cascader-component>
                </el-form-item>
                
                <el-form-item label="">
                    <cascader-component
                        v-model="staffSearch.service_category_id"
                        :cascaderName="''"
                        :modelType="'int'"
                        :setProps="setProps"
                        :requestUrl="'./admin/common/getServiceTree'"></cascader-component>
                </el-form-item>

                <el-form-item label="">
                    <cascader-component
                        v-model="staffSearch.ability_ids"
                        :cascaderName="''"
                        :setProps="setProps"
                        :requestUrl="'./admin/common/getLabelTree'"></cascader-component>
                </el-form-item>

                <el-form-item label="">
                    <cascader-component
                        v-model="staffSearch.paper_ids"
                        :cascaderName="''"
                        :setProps="setProps"
                        :requestUrl="'./admin/common/getPaperSelection'"></cascader-component>
                </el-form-item>

            </div>
        </el-form>
        
        <el-table :data="staffTable" class="staff-table">
            <el-table-column label="" prop="staff_code" align="center"></el-table-column>

            <el-table-column label="" prop="name" align="center"></el-table-column>
            
            <el-table-column label="" prop="age" align="center"></el-table-column>

            <el-table-column label="" prop="phone" align="center"></el-table-column>

            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="editStaff(scope.$index, scope.row)"></el-button>
                    <el-button size="mini" type="danger" v-if="scope.row.status == 0" @click="changeStaffStatus(scope.row)"></el-button>
                    <el-button size="mini" type="success" @click="changeStaffStatus(scope.row)" v-else></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
            class="pagination"
            @current-change="handleCurrentPage"
            @prev-click="handleCurrentPage"
            @next-click="handleCurrentPage"
            :current-page.sync="pagination.currentPage"
            :page-size="10"
            layout="prev, pager, next, jumper"
            :total="pagination.total"></el-pagination>
    </div>
</template>
<script>
    import hrService from '../../../../common/service/hrService.js'
    import cascaderComponent from '../../sale/components/cascaderComponent.vue'
    export default {
        components: {
            cascaderComponent
        },
        data() {
            return {
                //
                staffTable: [],
                //
                staffSearch: {
                    name: '', //
                    region_ids: [],//
                    service_category_id: 0,//
                    ability_ids: [],//
                    paper_ids: [],//
                },
                /**
                 * 
                 */
                setProps: {
                    label: 'name',
                    value: 'id'
                },
                /**
                 * 
                 */
                pagination: {
                    total: 0,
                    currentPage: 1,
                    pageNumber: 10,
                },
            }
        },
        computed:{
            /**
             * 
             */
            searchArray(){
                let arr = [],
                _this = this;

                Object.keys(this.staffSearch).forEach((item, index) =>{
                    //
                    if(Array.isArray(_this.staffSearch[item])){
                        if(_this.staffSearch[item].length){
                            let obj = {}
                            obj[item] = [..._this.staffSearch[item]]
                            obj = {
                                ...obj,
                                key: item
                            }
                            arr.push(obj)
                        }
                    } 
                    //
                    else if(_this.staffSearch[item] != ''){
                        let obj = {}
                        obj[item] = _this.staffSearch[item]
                        obj = {
                            ...obj,
                            key: item
                        }
                        arr.push(obj)
                    }
                })
                return arr
            }
        },
        methods: {
             /**
             * 
             * @param tableOption 
             * @param tableOption.currentPage 
             * @param tableOption.searchSelect Array 
             * [{searchkey: '', searchValue: ''}]
             */
            async getTableList(){

                let tableOption = {
                    currentPage: this.pagination.currentPage,
                    pageNumber: this.pagination.pageNumber,
                    get_for: 'staff',
                    searchSelect: this.searchArray
                }

                store.commit('setLoading',true)
                try{
                    await hrService.getStaffList(tableOption).then(data =>{
                            if(data.code == "0"){
                                this.staffTable = data.data.data
                                
                                //
                                this.pagination.currentPage = data.data.current_page //
                                this.pagination.total = data.data.total //
                            }
                        }).catch(error =>{
                            this.$message({
                                type:'error',
                                message: error.message
                            })
                        }).finally(() =>{
                            store.commit('setLoading',false)
                        })
                } catch(error){
                    this.$message({
                        type:'error',
                        message: error.message
                    })
                }
            },
            /**
             * 
             */
            async handleCurrentPage(val){
                this.pagination.currentPage = val
                await this.getTableList()
            },
            /**
             * 
             */
            async searchStaff(){
                await this.getTableList()
            },
            /**
             * 
             */
            async resetStaff(){
                this.staffSearch.name= '' //
                this.staffSearch.region_ids= []//
                this.staffSearch.service_category_id= 0//
                this.staffSearch.ability_ids= []//
                this.staffSearch.paper_ids= []//
                await this.getTableList()
            }, 
            /**
             * 
             * des 
             */
            createStaff(){
                this.$router.push({
                    path: "/staff/staffItem",
                    query: {
                        type: 0
                    }
                })
            },
            /**
             * 
             * 
             */
            editStaff(index, row){
                this.$router.push({
                    path: "/staff/staffItem",
                    query: {
                        type: 1, //1
                        id: row.id
                    }
                })
            },
            /**
             * changeStaffStatus
             */
            async changeStaffStatus(row){
                let _this= this;

                let status = row.status == 0? '' : ''
                
                let response = await this.$confirm(`${status}?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: `${status}`
                    });          
                });

                if(response == "confirm"){
                    store.commit('setLoading',1)

                    try{
                        await hrService.changeStaffStatus(row.id, row.version)
                            .then(data =>{
                                if(data.code == "0"){
                                    this.$message({
                                        type:'success',
                                        message: `${status}`
                                    })
                                }
                            }).catch(e =>{
                                this.$message({
                                    type:'error',
                                    message: e.message
                                })
                            })
                    } catch(error){
                        this.$message({
                            type:'error',
                            message: error.message
                        })
                    }

                    await _this.getTableList()
                    store.commit('setLoading',false)
                }
            }
        },
        async mounted(){
            await this.getTableList()
        }
    }
</script>
<style lang="scss" scoped>
        .staff{
        padding-top: 30px;
        margin: 0 auto;
        .staff-form{
            width:80%;
            min-width:1100px;
            margin: 0 auto;
            .search-up{
                display: flex;
                justify-content: space-between;
            }
        }
        .staff-table{
            width: 80%;
            min-width: 1100px;
            margin: 0 auto;
        }
        .pagination{
            width:80%;
            min-width:1100px;
            margin: 30px auto 0 auto;;
        }
    }
</style>



