<template>
    <div class="staff-box">
        <el-form class="staff-form" ref="form" :model="staffForm" :rules="staffRules" label-width="120px">
            <el-tabs v-model="activeName" :tab-position="'left'" @tab-click="tabChange">
                <el-tab-pane label="" name="require">

                    <el-form-item label="" prop="name">
                        <el-input size="small" v-model="staffForm.name" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="sex">
                        <el-radio-group v-model="staffForm.sex">
                            <el-radio :label="1"></el-radio>
                            <el-radio :label="2"></el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="" prop="age">
                        <el-input-number v-model="staffForm.age"></el-input-number>
                    </el-form-item>

                    <el-form-item label="" prop="phone">
                        <el-input v-model="staffForm.phone" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="identify">
                        <el-input v-model="staffForm.identify" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="address">
                        <el-input v-model="staffForm.address" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="bank_card">
                        <el-input v-model="staffForm.bank_card" placeholder=""></el-input>
                    </el-form-item>
                </el-tab-pane>

                <el-tab-pane label="" name="base">

                    <el-form-item label="">
                        <el-upload
                            class="avatar-uploader"
                            action="/admin/common/uploadImage"
                            :show-file-list="false"
                            :file-list="icon_fileList"
                            :on-success="iconUploadSuccess"
                            :before-upload="beforeAvatarUpload"
                            :headers="uploadHeader">

                            <div v-if="staffForm.icon!=''" class="avatar-box" @mouseover="showblack('0')" @mouseout="showblack('1')">
                                <img  :src="staffForm.icon == '' ? '' : `./resource/${staffForm.icon}`" class="avatar">
                                <div class="avatar-back" v-if="isShowBlack">
                                    <i class="el-icon-edit avatar-uploader-icon" style="color: #fff;font-size: 20px;"></i>
                                </div>
                            </div>

                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="" prop="nation">
                        <el-input v-model="staffForm.nation" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="wechat">
                        <el-input v-model="staffForm.wechat" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="education">
                        <el-select v-model="staffForm.education" placeholder="">
                            <el-option
                                v-for="(item, index) in educationList"
                                :key="index"
                                :label="item.name"
                                :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                </el-tab-pane>

                <el-tab-pane label="" name="skill">

                    <el-form-item label="" prop="region">
                        <tags-component
                        :set-props="setCascaderProps"
                        v-model="staffForm.region"
                        :optionList="areaList"
                        :tagName="''"
                        :idkey="'region_id'"></tags-component>
                    </el-form-item>

                    <el-form-item label="" prop="label">
                        <tags-component
                        :set-props="setCascaderProps"
                        v-model="staffForm.label"
                        :optionList="labelList"
                        :tagName="''"
                        :idkey="'ability_id'"></tags-component>
                    </el-form-item>

                    <el-form-item label="" prop="skill">
                        <tags-component
                        :set-props="setCascaderProps"
                        v-model="staffForm.skill"
                        :optionList="skillList"
                        :tagName="''"
                        :idkey="'service_category_id'"></tags-component>
                    </el-form-item>

                    <el-form-item label="" prop="paper">
                        <paper-component v-model="staffForm.paper"></paper-component>
                    </el-form-item>
                </el-tab-pane>
            </el-tabs>

            <el-form-item>
                <el-button type="primary" @click="onSubmit('form')">{{$route.query.id? '' : ''}}</el-button>
                <el-button @click="goback"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

/**
 * type 0   1 
 */
import {hrService} from '../../../../common'
import {hrRequest} from '../../../../common'

import tagsComponent from './tagsComponent.vue'
import paperComponent from './paperComponent.vue'


export default {
    components: {
        tagsComponent,
        paperComponent
    },
    data() {
        //
        const validator = {
            //
            nameValidate(rule, value, callback){
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (!/^[\u4e00-\u9fa5]+$/.test(value)) {
                        callback(new Error(''));
                    }
                    callback();
                }
            },
            //
            ageValidate(rule, value, callback){
                if (!value) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
            //
            phoneValidate(rule, value, callback){
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (!(/^1[34578]\d{9}$/.test(value))) {
                        callback(new Error(''));
                    }
                    callback();
                }
            },
            //
            identifyValidate(rule, value, callback){
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (!(value.length == 18)) {
                        callback(new Error('18'));
                    }
                    callback();
                }
            },
            //
            addressValidate(rule, value, callback){
                if (value === '') {
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
            //
            bankCardValidate(rule, value, callback){
                if (value === '') {
                    callback(new Error(''));
                }else if (!(/^[0-9]+$/.test(value))) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
            /************************************************************ */
            //
            nationValidate(rule, value, callback){
                if (value != '' && !(/^[\u4e00-\u9fa5]+$/.test(value))) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
        }
        return {
            icon_fileList: [],
            //
            staffForm: {
                id: '',//id

                icon: '',
                name: '',//
                identify: '',//
                sex: 1,//
                nation: '',//
                phone: '',//
                wechat: '',//
                region: [],//
                age: '',//
                address: '',//
                education: 0,//
                bank_card: '',//
                version: '',//,

                label: [],//
                paper: [],//
                skill: [],//
            },
            //
            staffRules: {
                //
                name: [
                    { validator: validator.nameValidate, trigger: 'blur' }
                ],
                //
                age: [
                    {validator: validator.ageValidate, trigger: 'blur'}
                ],
                //
                phone: [
                    {validator: validator.phoneValidate, trigger: 'blur'}
                ],
                //
                identify: [
                    {validator: validator.identifyValidate, trigger: 'blur'}
                ],
                //
                address: [
                    {validator: validator.addressValidate, trigger: 'blur'}
                ],
                //
                bank_card: [
                    {validator: validator.bankCardValidate, trigger: 'blur'}
                ],
                //
                nation: [
                    {validator: validator.nationValidate, trigger: 'blur'}
                ],
            },

            region: [],//
            areaList: [],//

            skill: [],//
            skillList: [],//

            label: [],//
            labelList: [],//

            //
            setCascaderProps: {
                label: 'name',
                value: 'id'
            },
            //tab
            activeName: 'require',
            isShowBlack: false,//
            //header
            uploadHeader:{
                accessToken: this.$store.state.loginModule.token.access_token
            }
        }
    },
    computed: {
        //
        educationList(){
            return this.$store.state.hrModule.educationList
        }
    },
    methods: {
        /**
         * 
         * 
         */
        async onSubmit(formName) {
            await this.$refs[formName].validate((valid, fileds) => {
                if (valid) {
                    hrService.editStaff(this.staffForm).then(data =>{
                            if(data.code == '0'){
                                this.$message({
                                    type:"success",
                                    message: ""
                                })
                                this.$router.push('/staff/staffList')
                            }
                        }).catch(error =>{
                            this.$message({
                                type:'error',
                                message: error.message
                            })
                        })
                } else {
                    let require = ["name", "sex", "phone", "identify", "address", "bank_card"],
                        base = ["nation", "wechat", "education"];
                    let errorKeys = Object.keys(fileds),
                        i = 0
                    while( i< errorKeys.length){
                        if(require.includes(errorKeys[i])){
                            this.activeName = "require"
                            break;
                        }
                        if(base.includes(errorKeys[i])){
                            this.activeName = "base"
                            break;
                        }
                        i++;
                    }
                    return false;
                }
            });
        },
        stopPropagation(){
            return false
        },
        showblack(type){
            if(type == '0'){
                this.isShowBlack = true
            } else {
                this.isShowBlack = false
            }
        },
        iconUploadSuccess(res, file) {
            this.staffForm.icon = res.data.path;

            this.icon_fileList =  [{
                url: `./resource/${this.staffForm.icon}`,
                name: 'head'
            }]
        },

        beforeAvatarUpload(file) {
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isLt2M) {
            this.$message.error(' 2MB!');
            }
            return isLt2M;
        },
        /**
         * 
         */
        goback(){
            this.$router.push("/staff/staffList")
        },
        /**
         * tab
         */
        tabChange(tab, event) {

        }
    },
    async mounted(){
        store.commit('setLoading',true)
        try{
            let data = await Promise.all([
                hrService.getAreaTree(),
                hrService.getSkillTree('enable'), //
                hrService.getAbilityTree('enable'), //
            ])
            //promise.all 
            this.areaList = data[0].data
            this.skillList = data[1].data
            this.labelList = data[2].data

            //
            if(this.$route.query.type == 1){
                await hrService.getStaff(this.$route.query.id)
                    .then(data =>{
                        if(data.code == "0"){
                            this.staffForm = data.data

                            this.staffForm.paper.forEach((item, index) =>{
                                item.images.forEach((it, index) =>{
                                    it.url = './resource/'+it.path
                                })
                            })
                            this.icon_fileList = this.staffForm.icon == ''? [] : [{
                                url: `./resource/${this.staffForm.icon}`,
                                name: 'head'
                            }]
                        }
                    }).catch(err =>{
                        throw err
                    })
            }
        }catch(e){
            this.$message({
                type:'error',
                message: e.message
            })
        }
        store.commit('setLoading',false)
    }
}
</script>
<style lang="scss" scoped>
    .staff-box{
        width: 80%;
        padding-top: 30px;
        .staff-form{
            max-width: 750px;
        }
        .paper-imgs{
            height: 110px!important;
            padding: 5px 0;
            .paper-item-img{
                display: inline-block;
                height:100px;
                width: 100px;
                margin : 0 10px;
            }
        }
        .image-messsage{
            line-height: 40px;
            display: flex;
            justify-content: space-between;
            padding: 0 20px;
        }
    }
    //
    .avatar-uploader /deep/ .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader /deep/ .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 16px;
        color: #8c939d;
        width: 65px;
        height: 65px;
        line-height: 65px;
        text-align: center;
    }
    .avatar-box{
        width:65px;
        height: 65px;
        position: relative;
        .avatar {
            width:65px;
            height: 65px;
            display: block;
        }
        .avatar-back{
            position: absolute;
            height: 65px;
            width: 65px;
            top: 0;
            z-index: 4;
            background: rgba(0,0,0,.5)
        }
    }

</style>





